html, body{
  padding: 0;
  margin: 0;
  min-height:100%;
  overflow-x: hidden;
}

path {
  stroke: #FFFFFF !important;
}

body {
  font-family: "consolas";
  text-align: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.searchBox {
  width: 100%;
  box-sizing: border-box;
}

.searchBox #searchBar {
  display: block; 
  height: 50px;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;

  appearance: none; 
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255,255,255,.5);
  border-radius: 0px 0px 16px 16px;
  font-size: 28px; 

  transition: .4s ease;
}

.searchBox #searchBar:focus {
  background-color: rgba(255,255,255,.75);
}

#welcome {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 5% 3%;
  color: white;
  border: 2px solid black;
  border-radius: 15px;
  background: rgba(0,0,0,.40);
  margin: 20% 0;
}

#welcome h1 {
  margin: 0;
  font-size: 1.5rem;
  padding: 5px;
  border: .5px solid white;
  border-radius: 10px;
  width: 70%;
}

.dragger {
  display:flex; 
  flex-direction: column;
}

.flex{
  display:flex;
  justify-content: center;
}

.weatherBox, #notFound, .units, .format {
  font-size: 20px;
  color:white;
  padding: 3% 0%;
  text-align: center;
  border: 1px solid black;
  border-radius: 15px;
  background: rgba(0,0,0,.40);
  min-width:95%;
  height: 30%;
}

.units, .format {
  display: flex;
  align-items: center;
  flex-direction: column;
  min-width: 40%;
  margin: 4% auto;
  padding: 2%;
}

.units p, .format p {
  margin: 0%;
}

.sliderContainer {
  display: flex;
  flex-direction: row;
}


#icon {
    height: auto; 
    width: auto; 
    max-width: 50px; 
    max-height: 50px;
}

#temp {
  padding-left: 45px;
  font-family: "Impact";
  font-size: 120px;
  margin: 0;
}

#temp span {
  font-family:"calibri"
}

#mainWeather{
  margin-top: 10px;
}

object {
  transform: scale(1.5);
}

#date, #location {
  margin: 0;
}

.forecastContainer{
  margin-right: 20%;
  min-width: 100%;
  display: inline-block;
}

/* Responsive Queries */
@media only screen and (min-width: 600px) {
  .weatherBox, #notFound, .forecastContainer .weatherBox{
    min-width: 60%;
    padding: 2%, 0%;
  }
  .units, .format { 
    min-width: 25%;
    margin: 2% 5%;
  }
  .sliderContainer{
    margin: 0 auto;
    width: 50%;
    display:flex;
    justify-content: center;
  }
  .dragger{
    width: 30%;
    min-height: 100%;
  }
  .forecastContainer {
    width: 100%;
  }
  .forecastContainer .weatherBox{
    width: 100%;
  }
  #welcome{
    margin:5% auto;
    width:50%;
  }
  #welcome h1{
    font-size: 3rem;
  }
}
